/**
 * basic hover animation timing (of color and opacity).
 * timing and bezier should be refactored after official Porsche animation guidelines are defined
 */
.teaser {
  margin-left: -2rem;
  margin-right: -2rem; }
  .teaser:first-child {
    margin-top: -2rem; }
    @media (min-width: 760px) {
      .teaser:first-child {
        margin-top: -4rem; } }
  @media (min-width: 760px) {
    .teaser {
      margin-left: -4rem;
      margin-right: -4rem; } }

.cover {
  position: relative;
  width: 100%;
  height: 30vw;
  overflow: hidden;
  background: #c8cacb;
  border-bottom: 1px solid #c8cacb; }
  .cover video {
    width: calc(100% + 4px);
    height: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

.intro {
  margin-left: -2rem;
  margin-right: -2rem;
  padding: 2rem; }
  @media (min-width: 760px) {
    .intro {
      margin-left: -4rem;
      margin-right: -4rem;
      padding: 4rem; } }

.newsletter {
  margin-left: -2rem;
  margin-right: -2rem;
  padding: 2rem;
  background: #eff0f1; }
  .newsletter:last-child {
    margin-bottom: -2rem; }
    @media (min-width: 760px) {
      .newsletter:last-child {
        margin-bottom: -4rem; } }
  @media (min-width: 760px) {
    .newsletter {
      margin-left: -4rem;
      margin-right: -4rem;
      padding: 4rem; } }
  .newsletter a {
    border-bottom: 1px solid #d5001c;
    text-decoration: none;
    color: #d5001c;
    transition: color 0.3s; }
    .newsletter a:hover {
      color: #960014; }
